body .ant-modal-mask {
  z-index: 99998;
}
body .ant-modal-wrap {
  z-index: 99999;
}
.ant-modal.dialog .ant-modal-content,
.ant-modal.dialog .ant-modal-header {
  border-radius: 5px;
}
.ant-modal.dialog .ant-modal-body {
  padding: 14px 14px 50px;
}
.ant-modal.dialog .close-dialog {
  width: 50px;
  height: 50px;
  position: absolute;
  right: -50px;
  top: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.ant-modal.dialog .close-dialog svg {
  width: 20px;
  height: 20px;
  color: white;
}
.ant-modal.dialog .later-dialog {
  margin-top: 20px;
  position: absolute;
  color: #0053FA;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.ant-modal.dialog .later-dialog span {
  text-decoration: underline;
}
.ant-modal.dialog .later-dialog svg {
  margin-right: 10px;
  vertical-align: middle;
}
.ant-modal.dialog .ant-modal-title {
  font-weight: 500;
  font-size: 17px;
  letter-spacing: 0;
  text-align: center;
}
.ant-modal.dialog .ant-modal-title svg {
  margin-right: 10px;
  vertical-align: middle;
}
.ant-modal.dialog.info .ant-modal-title {
  color: #0053FA;
}
.ant-modal.dialog.info .ant-modal-title svg {
  color: #0053FA;
}
.ant-modal.dialog.alert .ant-modal-title {
  color: #F0455A;
}
.ant-modal.dialog.alert .ant-modal-title svg {
  color: #F0455A;
}
.ant-modal.dialog .img-dialog {
  margin: 4px auto 0px;
  text-align: center;
  margin-bottom: 10px;
}
.ant-modal.dialog .img-dialog svg {
  max-width: 100%;
  max-height: 30vh;
}
.ant-modal.dialog .description-dialog {
  white-space: pre-line;
  font-weight: 500;
  font-size: 20px;
  color: #0D1B2A;
  letter-spacing: 0;
  text-align: center;
  margin-bottom: 17px;
}
.ant-modal.dialog.transfert-dialog .form-item-text {
  min-width: 350px;
  margin: 0 auto;
}
.ant-modal.dialog .body-dialog {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.ant-modal.dialog .body-dialog .ant-btn {
  display: inline-block;
  margin: 0 auto;
}
.ant-modal.dialog.code-dialog.loading .later-dialog {
  opacity: 0.5;
}
.ant-modal.dialog .ant-form-item {
  width: 80%;
}
